.AuthButton-button {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 50px;
    width: 200px;
    border-radius: 30px;
    border: 2.5px solid black;
    font-size: 18px;
    line-height: 18px;
    cursor: pointer;
    box-shadow: 0 5px 0 rgba(17, 17, 31, 0.25);
    transition: margin 0.1s linear, box-shadow 0.1s linear;
}

.AuthButton-button:hover {
    background: #1db954;
    color: white;
    margin-top: 2px;
    margin-bottom: -2px;
    box-shadow: 0 3px 0 rgba(17, 17, 31, 0.5);
}

.AuthButton-hovered {
    -webkit-animation: fadeSlideIn ease-out 300ms;
            animation: fadeSlideIn ease-out 300ms;
}

@-webkit-keyframes fadeSlideIn {
    from {
        opacity: 0;
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
    }
    to {
        opacity: 1;
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}
@keyframes fadeSlideIn {
    from {
        opacity: 0;
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
    }
    to {
        opacity: 1;
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}

