.Header-underline {
    position: absolute;
    bottom: 8px;
    height: 3px;
    background-color: black;
    transition: width 700ms cubic-bezier(0.23, 1, 0.32, 1);
    transition: left 700ms cubic-bezier(0.23, 1, 0.32, 1);
    display: none;
}

.Header-underline-show {
    display: block;
    -webkit-animation: fadeInAnimation ease 500ms;
            animation: fadeInAnimation ease 500ms;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes fadeInAnimation { 
    0% { 
        opacity: 0; 
    } 
    100% { 
        opacity: 1; 
    } 
}

@keyframes fadeInAnimation { 
    0% { 
        opacity: 0; 
    } 
    100% { 
        opacity: 1; 
    } 
